const TEMPLATE = `
    <div style="display:flex;align-items:center;justify-content:center;flex:1;flex-direction:column;">
        <div>
            <h1 id="CHUCKNORRIS_JOKE">...</h1>
        </div>
        <br/>
        
        <button id="CHUCKNORRIS_BUTTON">More</button>
        
        <br/>
        <hr/>
        <br/>
        
        <div>
            <p>Beware, Chuck knows your name: <span id="CHUCK_KNOWS_YOUR_NAME"></span></p>
        </div>
    </div>
`
export default class ChuckApp {

    constructor() {
        this.root = null;
        this.jokeNode = null;
        this.button = null;
        this.name = null;
        this.api = window.OpsPortalApi || null;
    }

    static getPluginId() {
        return 'chuck';
    }

    attachPlugin(id) {
        this.root = document.querySelector(id);
        if (this.root) {
            this.init();
        }
    }

    attachPluginToNode(node) {
	this.root = node;
	if(this.root) {
		this.init();
	}
    }

    init() {
        const fragment = document.createRange().createContextualFragment(TEMPLATE);
        this.jokeNode = fragment.querySelector("#CHUCKNORRIS_JOKE");
        this.btn = fragment.querySelector("#CHUCKNORRIS_BUTTON");
        this.btn.onclick = () => this.moreJokes();

        this.name = fragment.querySelector("#CHUCK_KNOWS_YOUR_NAME");
        if (this.api && this.api.getUserName) {
            this.name.innerHTML = this.api.getUserName();
        } else {
            this.name.innerHTML = "CANT GET NAME O.o !!! )."
        }

        this.root.appendChild(fragment);

       
        this.moreJokes();

    }

    moreJokes() {
        this.getJoke().then(
            jokeText => {
                this.jokeNode.innerHTML = jokeText;
            }
        )
    }

    async getJoke() {
        const response = await fetch('https://api.chucknorris.io/jokes/random');
        const myJson = await response.json();
        return myJson.value;
    }

}
